import React from 'react';
import { Sigma } from 'sigma';

type Node = {
    id: string;
    isModule?: boolean;
    isFirst?: boolean;
    type?: string;
    file?: string;
    layer?: number;
    column?: number;
};

type Data = {
    nodes: Node[];
    edges: {
        a: string;
        b: string;
    }[];
};

export const createTree = (data: Data, sigma: Sigma) => {
    const tree = new Set<string>();

    data.nodes.forEach(el => {
        if (!el.file) {
            return;
        }

        const parts = el.file.split(/(\/|\\)/g);
        const cwd: string[] = [];

        for (const part of parts) {
            cwd.push(part);
            tree.add(cwd.join('\\'));
        }
    });

    console.log(tree);

    return class Tree extends React.Component {
        render() {
            return 'Hello';
        }
    }
};
